@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Poppins:wght@500&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --color1: black;
    --color2: white;
    --color3: rgb(201, 201, 101);
    --color4: orange;
    --color5: rgb(116, 116, 163);
    --color6: rgb(45, 45, 95);
}

body {
    font-family: "Poppins";
    margin: 0;
    padding: 0;
    background-image: url("./assets/background.png");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.header {
    display: flex;
    justify-content: center;
}

.main {
    width: 100%;
    max-height: 650px;
    display: grid;
    grid-template-columns: 400px;
    margin-top: 4rem;
    justify-content: space-evenly;
}

.start {
    width: 18rem;
    height: 10rem;
    color: var(--color2);
    text-align: center;
    padding-left: 4rem;
    display: none;
}

.start--active {
    width: 18rem;
    height: 10rem;
    color: var(--color2);
    text-align: center;
    padding-left: 4rem;
    display: block;
}

.start-button {
    margin-top: 5rem;
    font-size: 1.25rem;
    height: 2.5rem;
    background-color: var(--color5);
    border: none;
    border-radius: 0.5rem;
    padding: 0.5rem;
    color: var(--color2);
    cursor: pointer;
    transition: all 0.25s ease-in-out;
}

.start-button:hover {
    color: var(--color2);
    background-color: var(--color4);
    font-weight: 600;
}

.game {
    position: relative;
    width: 420px;
}

.game-heading {
    color: var(--color2);
    margin-bottom: 15px;
}

.svg1 {
    position: absolute;
    right: 8%;
    top: -30px;
    height: 100px;
}

.svg2 {
    width: 75%;
    align-self: center;
    margin-top: -3rem;
}

.game__section {
    max-width: 415px;
    margin-left: -0.5rem;
    height: 520px;
    border-radius: 1rem;
    background-color: var(--color2);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 2rem;
    margin-bottom: 10px;
}

.question {
    color: var(--color6);
}

.options {
    border: 1px solid black;
    line-height: 3rem;
    display: flex;
    border-radius: 0.5rem;
    border: 1px solid grey;
    color: grey;
    min-height: 30px;
    cursor: pointer;
}

.options:hover {
    color: var(--color2);
    background-color: var(--color4);
}

.part1 {
    display: flex;
    width: 90%;
}

p {
    margin-right: 1rem;
    margin-left: 0.5rem;
}

label {
    cursor: pointer;
}

.next-button--active {
    opacity: 1;
    font-size: 1rem;
    line-height: 2rem;
    background-color: var(--color5);
    border: none;
    width: fit-content;
    padding-left: 1rem;
    padding-right: 1rem;
    align-self: flex-end;
    border-radius: 0.5rem;
    color: var(--color2);
    cursor: pointer;
    transition: color 0.25s ease-in-out;
}

.next-button {
    opacity: 0;
}

.next-button--active:hover {
    color: var(--color2);
    background-color: var(--color4);
}

.center-score {
    align-self: center;
}

.big {
    font-size: 2rem;
    color: green;
}

@media screen and (max-width: 720px) {
    .game {
        margin-left: 1rem;
    }
}

@media screen and (max-width: 420px) {
    .main {
        grid-template-columns: 280px;
    }
    .svg1 {
        height: 75px;
        top: 0px;
        left: 60%;
    }
    .header-image {
        width: 300px;
        margin-left: 2rem;
        height: 8rem;
    }
    .start--active {
        padding-left: 0px;
    }
    .game__section {
        width: 430px;
    }
    .game {
        margin-left: -0.75rem;
    }
}
